////// GLOBAL ////////
function display_search() {
    $('.search').click(function() {
        $('.searchbox').toggleClass('active')
    })
}

function mainFixed() {
    var header = $('header').outerHeight()
    $('main').css({
        'padding-top': header + 'px'
    })
}
const navMapping = () => {
    return new MappingListener({
        selector: 'ul.nav.social',
        mobileWrapper: '.sidemenu',
        mobileMethod: 'appendTo',
        desktopWrapper: '.phanam-header-1 .mainnav .nav-top',
        desktopMethod: 'appendTo',
        breakpoint: 992
    }).watch();
}
const navMapping_lang = () => {
    return new MappingListener({
        selector: '.language',
        mobileWrapper: '.sidemenu',
        mobileMethod: 'appendTo',
        desktopWrapper: '.phanam-header-1 .mainnav .nav-top',
        desktopMethod: 'appendTo',
        breakpoint: 992
    }).watch();
}

function slider_banner() {
    if ($('.phanam-banner-1 .list-items').length) {
        $(".phanam-banner-1 .list-items").slick({
            autoplay: true,
            // slickPlay: true,
            // slickPause: true,
            autoplaySpeed: 4000,
            dots: true,
            infinite: true,
            speed: 300,
            arrows: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            // customPaging: function(slider, i) {
            //     var thumb = $(slider.$slides[i]).data('thumb');
            //     return '<a><p> ' + thumb + '</p></a>';
            // },
        });
    }

};

$(function() {
        $('.phanam-header-1 .btn-open-menu').on('click', function() {
            $('.phanam-header-1 .btn-open-menu').toggleClass('active')
            $('.sidemenu').toggleClass('active')
            $('.phanam-header-1 .nav-bottom').toggleClass('active')
            $(' .sidemenu-backdrop').toggleClass('active')
        })
        $(' .sidemenu-backdrop').on('click', function() {
            $('.phanam-header-1 .btn-open-menu').toggleClass('active')
            $('.sidemenu').toggleClass('active')
            $('.phanam-header-1 .nav-bottom').toggleClass('active')
            $(' .sidemenu-backdrop').toggleClass('active')
        })
    })
    ////// END OF GLOBAL ////////


var slider_home = () => {
    if ($('.phanam-slider-1 .list-items').length) {
        $(".phanam-slider-1 .list-items").slick({
            autoplay: true,
            // slickPlay: true,
            // slickPause: true,
            autoplaySpeed: 4000,
            pauseOnHover: true,
            dots: false,
            infinite: true,
            speed: 300,
            arrows: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            // customPaging: function(slider, i) {
            //     var thumb = $(slider.$slides[i]).data('thumb');
            //     return '<a><p> ' + thumb + '</p></a>';
            // },
        });
    }

};


var setHeightH3 = () => {
    var maxHeight = 0;

    $('.phanam-intro-about-us-3 .block-wrapper .info-wrapper').each(function() {
        var thisH = $(this).height();
        if (thisH > maxHeight) {
            maxHeight = thisH;
        }
    });
    $('.phanam-intro-about-us-3 .block-wrapper .banner-info-wrapper').height(maxHeight)
    $('.phanam-intro-about-us-3 .block-wrapper .info-wrapper').height(maxHeight)
}

function accordion() {
    $('.hotro-1 .question-wrapper .item .title').on('click', function() {
        if ($(this).parents('.item').find('.content').is(':hidden') == true) {
            $('.hotro-1 .question-wrapper .item .title').removeClass('active')
            $(this).addClass('active')
            $('.hotro-1 .question-wrapper .item .content').slideUp()
            $(this).parents('.item').find('.content').slideDown()

        } else {
            $(this).removeClass('active')
            $(this).parents('.item').find('.content').slideUp()
        }
    })
}





///////// MAIN Control /////
$(document).ready(function() {
    // $('.phanam-news-photo-1  .box-zoom a').fancybox({
    // 	width: 640,
    // 	height: 400,
    // 	type: 'iframe'
    // });
    $('.image-map-container img').maphilight({
        strokeWidth: 0,
        strokeOpacity: 0
    })
    mainFixed()
    accordion()

    // 	$(".phanam-news-photo-1  .box-zoom").lightGallery({
    // 	thumbnail: true,
    // 	selector: 'a'
    // });
    /// GLOBAL ///
    AOS.init();

    /// END GLOBAL ///
    /// HEADER ///
    display_search();
    navMapping();
    navMapping_lang();
    slider_banner();
    slider_home()
        /// END HEADER ///
    if ($(window).width() < 768) {
        setHeightH3()

    }
});
$(window).resize(function() {
    if ($(window).width() < 768) {
        setHeightH3()

    }

});
$(document).ready(function(e) {
    $(".gioithieu2-1 map").imageMapResize();

})

function popup_gioithieu2() {
    $('.gioithieu2-1 map area').on('click', function() {
        let i = $(this).attr('data-popup')
        console.log(i)
        $('.gioithieu2-1 .popup-wrapper .popup-container .popup-content').hide()
        $('.gioithieu2-1 .popup-wrapper .popup-container .popup-content[data-map=' + i + ']').show()
        $('.gioithieu2-1 .popup-wrapper .popup-container .popup-content[data-map=' + i + ']').parents('.popup-wrapper').addClass('active')
    })
    $('.gioithieu2-1 .popup-wrapper .popup-container .popup-close').on('click', function() {
        $(this).parents('.popup-wrapper').removeClass('active')
    })
}
$(document).ready(function() {
    popup_gioithieu2()
    if ($(".cohoinghenghiep-1 .video").length > 0) {
        $(".cohoinghenghiep-1 .video").lightGallery();
    }
    if ($(".sukiennoibat-1 .video").length > 0) {
        $(".sukiennoibat-1 .video").lightGallery();
    }
    var swiper = new Swiper('.dichvu-3 .swiper-container', {
        loop: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        spaceBetween: 20,
        autoHeight: true,
        breakpoints: {
            768: {
                spaceBetweenSlides: 20
            }
        },
        navigation: {
            nextEl: '.swiper-next',
            prevEl: '.swiper-prev',
        },
    })
})